.header {
    height: 34vw;
    margin: 30px auto;
    background: url('../assets/header_img.png') no-repeat;
    background-size: contain;
    position: relative;
}
.header-contents {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;
}
.header-contents h2 {
    font-weight: 500;
    color: white;
    font-size: max(4.5vw, 22px);
    text-shadow: 2px 2px black;
}
.header-contents p {
    color: white;
    font-size: 1vw;
    text-shadow: 1px 1px black;
}
.header-contents button {
    border: none;
    color: #747474;
    font-weight: 500;
    padding: 1vw 2.3vw;
    background-color: white;
    font-size: max(1vw, 13px);
    border-radius: 50px;
}