.food-item {
    width: 100%;
    margin: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 3px #000015;
    transition: 0.3s;
    animation: fadeIn 1s;
}
.food-item-image {
    width: 100%;
    border-radius: 15px 15px 0px 0px;
}
.food-item-info {
    padding: 20px;
}
.food-item-name-rating {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}
.food-item-name-rating p {
    font-size: 16px;
    font-weight: 500;
}
.food-item-name-rating img {
    width: 70px;
}
.food-item-description {
    color: #262626;
    font-size: 14px;
}
.food-item-price {
    color: tomato;
    font-size: 22px;
    font-weight: 500;
    margin: 10px 0px;
}